$primary: #2f2e41;
$complimentary: #fff;
$accent: #f6b408;

@mixin tablet {
  @media only screen and (min-width: 768px) {
    @content;
  }
}

.search {
  background-color: $complimentary;
  .search-title {
    color: $primary;
    font-family: Montserrat;
    font-weight: bolder;
  }

  .hr {
    // background: black;
    width: 90%;
  }

  form {
    #settings_slider {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      display: none;
      background: $primary;
      padding: 10px 20px;
      width: 60%;
      // transition: 0.5s ease;

      @include tablet {
        display: block !important;
        position: unset;
        background: unset;
        padding: unset;
        width: unset;
      }

      #settings_cross {
        position: relative;
        margin: 10% 10px 15px 85%;

        @include tablet {
          display: none;
        }
        .div1,
        .div2 {
          background: $complimentary;
          height: 3px;
          width: 27px;
          margin-bottom: 3px;
        }

        .div1 {
          transform: rotate(45deg);
        }
        .div2 {
          transform: rotate(-45deg) translate(3.5px, -4px);
        }
      }
    } //settings_slider

    .group {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      border-radius: 5px;
      background: $primary;
      color: $complimentary;
      cursor: pointer;

      @include tablet {
        flex-direction: row;
        justify-content: start;
        align-items: center;
      }
    }
  }
}
