$primary: #2f2e41;
$complimentary: #fff;
$accent: #f6b408;

@mixin tablet {
  @media only screen and (min-width: 768px) {
    @content;
  }
}

.header {
  background-color: $primary;
  color: $complimentary;
}
#ham,
#settings {
  .div1,
  .div2,
  .div3 {
    background: $complimentary;
    height: 3px;
    width: 27px;
    margin-bottom: 3px;
  }
}

#slider {
  background: $primary;
  position: fixed;
  top: 0;
  bottom: 0;
  right: -70%;
  z-index: 10;
  width: 70%;
  transition: 0.5s ease;

  @include tablet {
    position: static;
    width: unset;
    top: unset;
    bottom: unset;
    transition: unset;
  }

  #cross {
    position: relative;
    margin: 10% 10px 15px 85%;

    @include tablet {
      display: none;
    }
    .div1,
    .div2 {
      background: $complimentary;
      height: 3px;
      width: 27px;
      margin-bottom: 3px;
    }

    .div1 {
      transform: rotate(45deg);
    }
    .div2 {
      transform: rotate(-45deg) translate(3.5px, -4px);
    }
  }
}
.link {
  color: $complimentary;
  &:hover {
    color: $accent;
  }
}
.button {
  // width: 150px;
  border: 1px solid $complimentary;
  border-radius: 5px;
  padding: 10px;
  // margin-top: 10px;
  font-weight: bold;
  &:hover {
    border-color: $accent;
  }
}
